    authDomain: "betterstudentvue.firebaseapp.com",
    projectId: "betterstudentvue",
    storageBucket: "betterstudentvue.appspot.com",
    messagingSenderId: "441663558373",
    appId: "1:441663558373:web:a5581f5fdd5387fea5ba0c",
